import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

/*  */
const date = 'Mon Oct 17 2016 00:00:00 GMT+0800 ()'
const ruleForm = {
    name: '',
    fenLeis:[
        {name: ''},
        {name: ''},
        {name: ''}
    ],
    fenLei: '',
    tags: [{name:''}],
    activeStartTimeDate: date,
    activeStartTimeTime: '00:45',
    activeEndTimeDate: date,
    activeEndTimeTime: '00:45',
    signStartTimeDate: date,
    signStartTimeTime: '00:45',
    signEndTimeDate: date,
    signEndTimeTime: '00:45',
    activePerson:'',
    activePersonNum:'',
    activeDescribe:'',
    UseMsgShow:'',
    evaluate:'',
    adTitle:'',
    adContent:'',
    adLink:'',
    province:'',
    city:'',
    detail:''
}

const signForm ={
    signUpLimit: '',
    numLimit: '',
    numLimitDetail: '',
    cost: '',
    costDetail: '',
    cancel: '',
    audit: '',
    needName: true,
    needTel: true,
    signFormList:[
        {title:'',require:true},
        {title:'',require:true},
        {title:'',require:true},
        {title:'',require:true}
    ],
    sign: '',
    signType: '',
    secretCode: ''
}

const shareForm ={
    title: 'Allen',
    describe: ''
}

const selfForm ={
    signUpSuccess:true,       // 
    signUpSuccessText:'***()',
    auditSuccess:true,
    auditSuccessText:'***()',
    auditFailed:true,
    auditFailedText:'***()',
    signInSuccess:true,
    signInSuccessText:'***()',
    signInFailed:true,
    signInFailedText:'',
    signInDouble:true,
    signInDoubleText:'',
    remind:'',
    remindTime:'',
    remindText:'',
    useScore:'',
    useScoreNum:'',
    signUpScore:'',
    signUpScoreNum:'',
    shareScore:'',
    shareScoreNum:'',
    shareReadScore:'',
    shareReadScoreNum:'',
    shareReadScoreNumMax:'',
    shareSignUp:'',
    shareSignUpNum:'',
    shareSignUpNumMax:'',
    afterShare:'',
    afterShareLink:'',
    afterSingUp:'',
    afterSingUpLink:'',
    shareImg:'',
    shareImgUrl:''
}
const data = [
    {
        id:'1111',
        name: 'Allen',
        type:'',
        status:'',
        readNum:200,
        signUpNum:100,
        auditNum:5,
        activeMessage:{}
    },
    {
        id:'2222',
        name: '',
        type:'',
        status:'',
        readNum:200,
        signUpNum:100,
        auditNum:8,
        activeMessage:{}
    }
]
/*  */
/*
 * ruleForm  1
 * signFrom  2
 * shareFrom 3
 * selfFrom  4
 * activeList 
 * */
const state = {
    ruleForm: ruleForm,
    signForm: {},
    shareForm: {},
    selfForm: {},
    activeList:data
}

/*  */
for(var item in state){
  localStorage.getItem(item)?
    state[item] = JSON.parse(localStorage.getItem(item)): false;
}

const mutations = {
    setRuleForm(state, payload) {
        Object.assign(state.ruleForm, payload);
        localStorage.setItem('ruleForm',JSON.stringify(payload));
    },
    setSignForm(state, payload) {
        Object.assign(state.signForm, payload);
      localStorage.setItem('signForm',JSON.stringify(payload));

    },
    setShareForm(state, payload) {
        Object.assign(state.shareForm, payload);
      localStorage.setItem('shareForm',JSON.stringify(payload));
    },
    setSelfForm(state, payload) {
        Object.assign(state.selfForm, payload);
      localStorage.setItem('selfForm',JSON.stringify(payload));
    }
}

export default  new Vuex.Store({
    state,
    mutations
})



